.dash-content {
  padding: 0;
}

.dash-navbar {
  height: 150vh;
}

.security-head {
  background: #fff;
  width: 100%;
  padding: 20px 27px;
  font-size: 29px;
  font-weight: 600;
}

.main-security {
  margin-top: 30px;
  padding: 1px 27px;
}

.the-factor {
  justify-content: space-between;
  align-items: center;
  gap: 1.9rem;
  display: flex;
}

.enable {
  color: #fff;
  background-color: #4e6fff;
  border-radius: 4px;
  padding: 8px 20px;
  font-size: 14px;
}

.setup {
  background-color: #fff;
  border-radius: 4px;
  padding: 8px 18px;
  font-size: 12.2px;
  box-shadow: 0 0 4px #0003;
}

.factor-authentication {
  background: #fff;
  border-radius: 14px;
  padding: 19px 24px;
  box-shadow: 0 0 4px #0003;
}

.the-factor h4 {
  margin-bottom: 12px;
  font-size: 19px;
  font-weight: 800;
}

.the-factor h5 {
  margin-bottom: 5px;
  font-size: 16px;
  font-weight: 800;
}

.the-factor p {
  color: #00000080;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.6;
}

.factor-top {
  border-top: 1px solid #0000001a;
  padding: 8px 0;
}

.password-left h4 {
  margin-bottom: 12px;
  font-size: 19px;
  font-weight: 800;
}

.password-left p {
  color: #00000080;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.6;
}

.topper {
  margin-top: 8px;
}

.change-password {
  background: #fff;
  border-radius: 14px;
  margin-top: 30px;
  margin-left: 2px;
  padding: 19px 18px;
  box-shadow: 0 0 4px #0003;
}

label {
  color: #000c;
  margin: 15px 0 5px;
  font-family: inherit;
  font-weight: 800;
}

.change-password input {
  border: none;
  border-bottom: 1px solid #00000030;
  width: 100%;
  height: 33px;
  padding: 3px 8px;
}

:focus {
  outline: none;
}

.password-rules Ul {
  margin-top: 20px;
  margin-left: -20px;
  font-size: 12px;
  font-weight: 600;
  line-height: 2;
  list-style-position: outside;
}

.password-rules {
  color: #0009;
  background: #e5ecf7;
  border-radius: 10px;
  padding: 15px;
}

.password-rules p {
  font-size: 12px;
  font-weight: 600;
}

.password-rules h5 {
  color: #000c;
  font-weight: 800;
}

.password-right {
  padding: 5px 20px;
}

.devices {
  background: #fff;
  border-radius: 14px;
  padding: 20px 25px;
  box-shadow: 0 0 4px #0003;
}

.device-image {
  color: #fff;
  background: #ff9293;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 60px;
  height: 60px;
  font-size: 21px;
  display: flex;
}

.device-head {
  margin-top: 14px;
}

.device-head h4 {
  font-size: 20px;
  font-weight: 800;
}

.device-head p {
  color: #000000b3;
}

#sign-out {
  color: #fff;
  background: #4e6fff;
  border-radius: 5px;
  margin-top: 8px;
  padding: 10px 13px;
  font-size: 13px;
  font-weight: 600;
}

.phone-laptop-devices {
  border-top: 1px sold #00000030;
  padding: 15px 0;
}

.device1 {
  border-top: 1px solid #0000001a;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
  display: flex;
}

.device-left {
  font-size: 13px;
  font-weight: 800;
}

.device-left span {
  color: #0006;
  margin-left: 29px;
  font-size: 12px;
}

.device-left i {
  margin-right: 10px;
  font-size: 16px;
  font-weight: 800;
}

@media screen and (width <= 1100px) {
  .factor-authentication {
    box-shadow: none;
    background: #fff;
    border-radius: 0;
    width: 100%;
    padding: 19px 0;
  }

  .dash-content {
    background: #fff;
  }

  .factor-left {
    width: 60%;
    overflow-y: hidden;
  }

  .the-factor h4, h5 {
    overflow-y: hidden;
  }

  .change-password {
    box-shadow: none;
    background: #fff;
    border-radius: 0;
    width: 100%;
    padding: 19px 0;
  }

  .change-password h4 {
    overflow-y: hidden;
  }

  .password-left, .password-right {
    padding: 0;
  }

  .password-rules {
    margin-top: 30px;
  }
}

